import { Helmet } from 'react-helmet-async'
import { useParams } from 'react-router'
import { Link as RouterLink } from 'react-router-dom'
import { useRecoilValue } from 'recoil'

import {
  Box,
  Button,
  Card,
  CardHeader,
  Container,
  Divider,
  Stack,
  type Theme,
  Typography,
  useMediaQuery
} from '@mui/material'

import DateForm from 'src/components/DateForm'
import PageTitleWrapper from 'src/components/PageTitleWrapper'

import useSafeAuthUser from 'src/hooks/useSafeAuthUser'
import { dateState } from 'src/recoil/atom'

import PageHeader from '../PageHeader'
import MemberGrid from './MemberGrid'

function Member() {
  const date = useRecoilValue(dateState)
  const { id } = useParams()
  const { userInfo } = useSafeAuthUser()
  const matches = useMediaQuery((theme: Theme) => theme.breakpoints.up('sm'))

  const clubId = Number(id)
  const club = userInfo?.clubs.find(club => club.id === clubId)

  return (
    <>
      <Helmet>
        <title>{club?.name ?? ''} 회원 명단 - KLUB</title>
      </Helmet>
      <PageTitleWrapper>
        <PageHeader clubName={club?.name ?? ''} />
      </PageTitleWrapper>
      <Container style={{ width: '100%' }} sx={{ px: matches ? 2 : 0 }}>
        <DateForm club={club} />
        <Card>
          <Box
            display="flex"
            alignItems="baseline"
            flexDirection={matches ? 'row' : 'column'}
            justifyContent="space-between"
          >
            <Box display={'flex'} alignItems={'baseline'}>
              <CardHeader title="회원 명단" />
              <Typography variant={'caption'}>명예회원은 별도로 표시돼요</Typography>
            </Box>
            <Stack direction={matches ? 'row' : 'column'} spacing={1} paddingX={1} width={matches ? 'unset' : '100%'}>
              <Button
                component={RouterLink}
                to={`/manage/${clubId}/member/honorary`}
                variant="contained"
                color="success"
                fullWidth={!matches}
              >
                명예회원 보기
              </Button>
              <Button
                component={RouterLink}
                to={`/manage/${clubId}/member/migration`}
                variant="contained"
                fullWidth={!matches}
              >
                회원 추가
              </Button>
            </Stack>
          </Box>
          <Divider />
          <MemberGrid {...date} />
        </Card>
      </Container>
    </>
  )
}

export default Member
